import React, { useContext } from "react";
import DataTable from "react-data-table-component";
import Card from "@material-ui/core/Card";
import "./Tables.css";

import { MyContext } from "../../../../Context/MyProvider";
import { styles } from "../helpers";

const columns = [
  {
    name: "Parameters",
    selector: "Parameters",
    // grow: ,
    wrap: true,
    // width:'35px',
    conditionalCellStyles: [
      {
        when: row => row["Parameters"] === 'Buoyancy ratios[1]'
          || row["Parameters"] === 'Revenue Buoyancy[2] w.r.t GSDP'
          || row["Parameters"] === "State's own revenue buoyancy[3] w.r.t GSDP",
        style: {
          backgroundColor: 'rgba(153, 165, 128, 0.7)',
          color: 'white',
          '&:hover': {
            cursor: 'pointer',
          },
        },
      },
    ]
  },
  {
    name: "2017-18",
    selector: "2017 18",
    // grow: 0.05,
    wrap: true,
    format: data => data["2017 18"].toLocaleString('en-IN')
  },
  {
    name: "2018-19",
    selector: "2018 19",
    // grow: 0.05,
    // width:'110px',
    wrap: true,
    format: data => data["2018 19"].toLocaleString('en-IN')
  },
  {
    name: "2019-20",
    selector: "2019 20",
    // grow: 0.05,
    // width:'110px',
    wrap: true,
    format: data => data["2019 20"].toLocaleString('en-IN')
  },
  {
    name: "2020-21",
    selector: "2020 21",
    // grow: 0.05,
    // width:'110px',
    wrap: true,
    format: data => data["2020 21"].toLocaleString('en-IN')
  },
  {
    name: "2021-22",
    selector: "2021 22",
    // grow: 0.05,
    // width:'110px',
    wrap: true,
    format: data => data["2021 22"].toLocaleString('en-IN')
  }
];

const customStyles = styles;

const Table3 = () => {

  const ctx = useContext(MyContext)

  return (
    <div className="App" style={{ margin: "40px 0 40px 0" }} >
      <Card>
        <DataTable
          title="Table 2.3: Trends and growth in Revenue Receipts"
          columns={columns}
          data={ctx.tables2.Table3}
          customStyles={customStyles}
          striped
          // conditionalRowStyles={conditionalRowStyles}
          highlightOnHover
          pointerOnHover
        // pagination
        />
      </Card>
    </div>
  );
}

export default Table3;
